// @flow
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import VeeValidate from 'vee-validate'

import './assets/global.css'
import 'element-ui/lib/theme-default/index.css'
import locale from 'element-ui/lib/locale/lang/fa'

import App from './components/App'
import store from './store'

window.__disableNativeFetch = true
/* eslint-disable no-undef */
require('fetch-ie8')
require('animate.css')

const messages = {
    after: (field, [target]) => `    ${target} .`,
    alpha_dash: () => '           .',
    alpha_num: () => '       .',
    alpha_spaces: () => '        .',
    alpha: () => '      .',
    before: (field, [target]) => `    ${target} .`,
    between: (field, [min, max]) => `  ${min}  ${max}  .`,
    confirmed: () => '    .',
    credit_card: () => '  .',
    date_between: (field, [min, max]) => `   ${min} and ${max} .`,
    date_format: (field, [format]) => `   ${format} .`,
    decimal: (field, [decimals] = ['*']) => `    ${decimals === '*' ? '' : '   ' + decimals + ' '} .`,
    digits: (field, [length]) => `      ${length}  .`,
    dimensions: (field, [width, height]) => `   ${width}    ${height}   .`,
    email: () => '     .',
    ext: () => '    .',
    image: () => '   .',
    in: () => '    .',
    ip: () => '      .',
    max: (field, [length]) => `   ${length}  .`,
    max_value: (field, [max]) => `  ${max}   .`,
    mimes: () => '    .',
    min: (field, [length]) => `  ${length}  .`,
    min_value: (field, [min]) => `  ${min}   .`,
    not_in: () => '    .',
    numeric: () => '    .',
    regex: () => '   .',
    required : () => '   ',
    size: (field, [size]) => `   ${localizeSize(size)} .`,
    url: () => '    .'
}

const dictionary = {
    fa: {
        messages,
        attributes:{}
    }
};

const veeValidateConfig = {
    errorBagName: 'veeErrors', // change if property conflicts.
    fieldsBagName: 'veeFields',
    delay: 0,
    locale: 'fa',
    dictionary,
    strict: true,
    classes: false,
    classNames: {
        touched: 'touched', // the control has been blurred
        untouched: 'untouched', // the control hasn't been blurred
        valid: 'valid', // model is valid
        invalid: 'invalid', // model is invalid
        pristine: 'pristine', // control has not been interacted with
        dirty: 'dirty' // control has been interacted with
    },
    events: 'input|blur',
    inject: false,
    validity: true,
    aria: true
}

Vue.use(VeeValidate, veeValidateConfig)

Vue.use(ElementUI, { locale })

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    template: '<App class="new"/>',
    components: { App },
    store
})



